.ProductDetails {
  width: 100vw;
  max-width: 100%;
  padding: 6vmax;
  box-sizing: border-box;
  display: flex;
}

.ProductDetails > div {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 2vmax;
  box-sizing: border-box;
  border: thin;
}

.ProductDetails > div:last-child {
  align-items: flex-start;
}

.CarouselImage {
  width: 20vmax;
}

.detailsBlock-1 > h2 {
  color: rgb(54, 54, 54);
  font: 600 1.8vmax "Raleway";
}

.detailsBlock-1 > p:first-of-type {
  color: rgba(54, 54, 54, 0.582);
  font: 200 1vmax "Bodoni Moda";
}

.detailsBlock-1 > p {
  color: rgba(54, 54, 54, 0.582);
  font: 200 0.6vmax "Raleway";
}

.detailsBlock-2 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font: 200 0.8vmax "Raleway";
  border-top: 1px solid rgba(0, 0, 0, 0.205);
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  width: 70%;
  padding: 1vmax 0;
}

.detailsBlock-2-span {
  font: 300 0.8vmax "Raleway";
  margin-left: 0.5vmax;
}

.detailsBlock-3 {
  width: 70%;
}

.detailsBlock-3 > h1 {
  color: rgba(17, 17, 17, 0.795);
  font: 400 1.8vmax "Raleway";
  margin: 1vmax 0;
}

.detailsBlock-3-1 {
  display: flex;
  align-items: center;
}

.detailsBlock-3-1-1 > button {
  border: none;
  background-color: rgba(0, 0, 0, 0.616);
  padding: 0.5vmax;
  cursor: pointer;
  color: white;
  transition: all 0.5s;
  margin-inline-end: 0.5vmax;
}

.detailsBlock-3-1-1 > button:hover {
  background-color: rgba(0, 0, 0, 0.767);
}

.detailsBlock-3-1-1 > input {
  border: none;
  padding-left: 0vmax;
  padding-right: 0.5vmax;
  width: 1vmax;
  text-align: center;
  outline: none;
  font: 400 0.8vmax "Raleway";
  color: rgba(0, 0, 0, 0.74);
  margin: 0;
}

.detailsBlock-3-1 > button {
  width: 35%;
  display: flex;
  justify-content: center;
  border: none;
  cursor: pointer;
  background-color: rgb(24, 23, 23);
  color: #ccaa6e;
  font: 500 0.9vmax "Raleway";
  transition: all 0.4s;
  padding: 0.5vmax 2vmax;
  margin: 0.5vmax;
  outline: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}

.detailsBlock-3-1 > button:hover {
  background-color: rgb(68, 67, 67);
}

.detailsBlock-3 > p {
  border-top: 1px solid rgba(0, 0, 0, 0.205);
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  padding: 1vmax 0;
  color: rgba(0, 0, 0, 0.651);
  font: 400 1vmax "Raleway";
  margin: 1vmax 0;
}

.detailsBlock-4 {
  color: rgba(0, 0, 0, 0.897);
  font: 500 1.2vmax "Raleway";
}

.detailsBlock-4 > p {
  width: 80%;
  color: rgba(0, 0, 0, 0.534);
  font: 300 0.8vmax "Raleway";
}

.submitReview {
  border: none;
  cursor: pointer;
  background-color: #a77066;
  color: #ecc787;
  font: 500 0.9vmax "Raleway";
  padding: 0.8vmax 2vmax;
  margin: 1vmax 0;
  transition: all 0.4s;
  outline: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}

.submitReview:hover {
  background-color: #a88a84;
}
.submitDialog {
  display: flex;
  flex-direction: column;
}
.submitDialogTextArea {
  border: 1px solid rgba(0, 0, 0, 0.082);
  margin: 1vmax 0;
  outline: none;
  padding: 1rem;
  font: 300 1rem "Raleway";
}

.reviewsHeading {
  color: #000000be;
  font: 500 1.4vmax "Raleway";
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.226);
  padding: 1vmax;
  width: 20vmax;
  margin: auto;
  margin-bottom: 4vmax;
}

.reviews {
  display: flex;
  overflow: auto;
}

.reviewCard {
  flex: none;
  box-shadow: 0 0 5px rgba(126, 74, 74, 0.226);
  border: 1px solid rgba(56, 56, 56, 0.116);
  width: 30vmax;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1vmax;
  padding: 3vmax;
}

.reviewCard > img {
  width: 5vmax;
}

.reviewCard > p {
  color: rgba(0, 0, 0, 0.836);
  font: 600 0.9vmax "Raleway";
}

.reviewCardComment {
  color: rgba(0, 0, 0, 0.445);
  font: 300 0.8vmax "Raleway";
}

.noReviews {
  font: 400 1.3vmax "Raleway";
  text-align: center;
  color: rgba(0, 0, 0, 0.548);
}

@media screen and (max-width: 600px) {
  .ProductDetails {
    flex-direction: column;
    height: unset;
  }
  .ProductDetails > div:last-child {
    align-items: center;
  }

  .detailsBlock-1 > h2 {
    font-size: 2.8vmax;
    text-align: center;
  }

  .detailsBlock-1 > p {
    text-align: center;
    font-size: 1vmax;
  }

  .detailsBlock-2 {
    justify-content: center;
  }
  .detailsBlock-2 > span {
    font-size: 1.5vmax;
  }

  .detailsBlock-3 > h1 {
    font: 700 3vmax "Raleway";
    text-align: center;
  }

  .detailsBlock-3-1 {
    flex-direction: column;
  }

  .detailsBlock-3-1-1 {
    padding: 2vmax 0;
  }
  .detailsBlock-3-1-1 > button {
    padding: 1.2vmax;
    width: 4vmax;
  }

  .detailsBlock-3-1-1 > input {
    padding: 1.5vmax;
    width: 3vmax;
    font: 400 1.8vmax "Raleway";
  }

  .detailsBlock-3-1 > button:last-child {
    font: 500 1.7vmax "Raleway";
    padding: 1.5vmax;
    width: 20vmax;
    margin: 3vmax 0;
  }

  .detailsBlock-3 > p {
    padding: 2.5vmax 0;
    text-align: center;
    font: 400 2vmax "Raleway";
  }

  .detailsBlock-4 {
    font: 500 2.5vmax "Raleway";
  }

  .detailsBlock-4 > p {
    font: 300 1.8vmax "Raleway";
  }

  .submitReview {
    font: 500 1.7vmax "Raleway";
    padding: 1.5vmax;
    width: 20vmax;
    margin: 3vmax 0;
  }
}
